<template>
    <b-row>
        <b-col>

            <b-row class="mb-3">
                <b-col cols="6" md="4" lg="3">
                    <b-link :href="`/pages/calculator/group-score-pdf-create?array=${this.selectedActions}`"
                            target="_blank"
                        class="btn btn-primary"
                        :disabled="selectedActions.length === 0">
                        Зведений рахунок
                    </b-link>
                </b-col>
                <b-col cols="6"  md="4" lg="3" class="ml-n5">
                    <b-link :href="`/pages/calculator/create_group_pdf?array=${this.selectedActions}`"
                            target="_blank"
                            class="btn btn-primary"
                            :disabled="selectedActions.length === 0">
                        Зведена квитанція
                    </b-link>
                </b-col>
            </b-row>

            <b-table striped
                     hover
                     responsive
                     :items="value"
                     :fields="fields"
                     :filter="getFilterText"
                     :current-page="currentPage"
                     :per-page="perPage"
            >
                <template v-slot:cell(check)="row">
                    <b-form-checkbox
                            v-model="selectedActions"
                            :key="row.item.id"
                            :value="row.item.id"
                            v-if="row.item.type == 0"
                    >
                    </b-form-checkbox>
                </template>
