            </b-table>

            <p v-if="!value.length">Дані відстуні</p>

            <b-pagination
                    v-model="currentPage"
                    :total-rows="totalRows"
                    :per-page="perPage"
                    size="sm"
                    v-if="value.length"
            ></b-pagination>

            <InvoicePopup
                    :isActive="isOpenSavePayment"
                    :item="rowPaymentData"
                    @showPopup="successPayment"/>

            <MorePopup
                    :isActive="isOpenMore"
                    :item="getMoreInfo"
                    @showPopup="closeMore"/>

            <EditOrder
                    :isActive="isOpenEdit"
                    :item="editOrderInfo"
                    @showPopup="successEdit"
                    @hide="isOpenEdit= false"/>
        </b-col>
    </b-row>
</template>

<script>
    import InvoicePopup from '../popups/SavePayment';
    import MorePopup from '../popups/MoreInfo';
    import EditOrder from '../popups/EditOrder';
    import {mapActions, mapGetters, mapMutations} from 'vuex';

    export default {
        name: "ServicesTable",
        props: {
            filter: {
                type: String,
                default() {
                    return ''
                }
            },
            value: {
                type: Array, Object,
                default() {
                    return []
                }
            }
        },
        data() {
            return {
                totalRows: 1,
                currentPage: 1,
                perPage: 10,
                fields: [
                    {
                        key: "check",
                        label: "",
                    },
                    {
                        key: "code",
                        label: "Код дії",
                        sortable: true
                    },
                    {
                        key: "fio",
                        label: "ПІБ платника",
                        sortable: true
                    },
                    {
                        key: "count",
                        label: "Кількість",
                        sortable: true
                    },
                    {
                        key: "price",
                        label: "Вартість",
                        sortable: true
                    },
                    {
                        key: "created_at",
                        label: "Дата створення",
                        sortable: true
                    },
                    {
                        key: "action_date",
                        label: "Дата над.пос.",
                        sortable: true
                    },
                    {
                        key: "pay_date",
                        label: "Дата оплати",
                        sortable: true
                    },
                    {
                        key: "invoice",
                        label: "Номер квитанції",
                        sortable: true
                    },
                    {
                        key: "type",
                        label: "Статус",
                        sortable: true
                    },

                ],
                isOpenSavePayment: false,
                rowPaymentData: null,
                isShowDetails: false,
                isOpenMore: false,
                isOpenEdit: false,
                editOrderInfo: null,
                selectedActions: [],
            }
        },
        components: {
            InvoicePopup,
            MorePopup,
            EditOrder
        },
        computed: {
            ...mapGetters('Calculator', ['moreInfo']),
            getFilterText() {
                return this.filter
            },
            getMoreInfo() {
                return this.moreInfo || {}
            },
        },
        methods: {
            ...mapActions('Calculator', ['getOnceAction', 'getOnceService']),
            ...mapActions('Orders', ['deleteOrder', 'getGroupScore']),
            ...mapMutations('Calculator', ['clearMoreInfo']),
            getPDF(id) {
                this.$router.push(`pages/calculator/pdg-create/${id}`)
            },
            openInvoice(data) {
                this.selectedActions = [];
                this.isOpenSavePayment = true;
                this.rowPaymentData = data;
            },
            successPayment() {

                this.selectedActions = [];
                this.isOpenSavePayment = false;
                this.$emit('update');
            },
            successEdit() {
                this.selectedActions = [];
                this.isOpenEdit = false;
                this.$emit('update');
            },
            showDetails(record, index) {

                this.selectedActions = [];
                let services = JSON.parse(record.service_id);

                this.getOnceAction(record.action_id).then(() => {


                    services.forEach(item => {
                        this.getOnceService({id: item.service, count: item.count}).finally(()=> {
                            this.isOpenMore = true;
                        })
                    })
                })
            },
            closeMore() {
                this.isOpenMore = false;
                this.clearMoreInfo();
            },
            deleteAction(id) {

                if (confirm('Ви впевнені?')) {
                    this.deleteOrder(id).then(() => {
                        this.$emit('update');
                        this.selectedActions = [];
                    })
                }

            },
            openEdit(data) {
                this.isOpenEdit = true;
                this.editOrderInfo = {
                    id: data.item.id,
                    create: data.item.created_at,
                    fio: data.item.fio,
                    invoice: data.item.invoice,
                    pay_date: data.item.pay_date,
                    action_date: data.item.action_date,
                };
            },
        },
        created() {
            this.totalRows = this.value.length
        },
    }
</script>